import React, { Component } from "react";
import {
  Navbar,
  NavbarBrand,
  Nav,
  NavbarToggler,
  Collapse,
  NavItem,
  Jumbotron,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  Form,
  FormGroup,
  Input,
  Label
} from "reactstrap";
import { NavLink } from "react-router-dom";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isNavOpen: false,
      isModalOpen: false,
      isSignInOpen: false
    };
    this.toggleNav = this.toggleNav.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
    this.handleLogout = this.handleLogout.bind(this);

    this.toggleSignIn = this.toggleSignIn.bind(this); // for sign in
    this.handleSignIn = this.handleSignIn.bind(this); // for sign in
    //this.refreshPage = this.refreshPage.bind(this);
  }

  toggleNav() {
    this.setState({
      isNavOpen: !this.state.isNavOpen
    });
  }

  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  handleLogin(event) {
    this.toggleModal();
    this.props.loginUser({
      username: this.username.value,
      password: this.password.value
    });
    event.preventDefault();
  }

  toggleSignIn() {
    // for sign in
    this.setState({
      isSignInOpen: !this.state.isSignInOpen
    });
  }

  refreshPage() {
    window.location.reload();
  }

  handleSignIn(event) {
    // for sign in
    this.toggleSignIn();

    //this.props.loginUser({username: this.username.value, password: this.password.value}); // original
    // Add lastname and firstname for sign up function
    // console.log(
    //   "username: ",
    //   this.username.value,
    //   ", password: ",
    //   this.password.value
    // );
    this.props.SignInUser({
      username: this.username.value,
      password: this.password.value,
      firstname: this.firstname.value,
      lastname: this.lastname.value
    });
    // sned data to loginUser and then perform dispatch(receiveLogin(response)) in SignInUser to send data to receiveLogin()
    this.props.loginUser({
      username: this.username.value,
      password: this.password.value
    });
    event.preventDefault();
  }

  handleLogout() {
    this.props.logoutUser();
  }

  render() {
    return (
      <React.Fragment>
        <Navbar dark expand='md'>
          <div className='container'>
            <NavbarToggler onClick={this.toggleNav} />
            <NavbarBrand className='mr-auto' href='/'>
              <img
                src='assets/images/logo.png'
                height='50'
                width='50'
                alt='Fancy Japanese Food'
              />
            </NavbarBrand>
            <Collapse isOpen={this.state.isNavOpen} navbar>
              <Nav navbar>
                <NavItem>
                  <NavLink className='nav-link' to='/home'>
                    <span className='fa fa-home fa-lg'></span> Home
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className='nav-link' to='/aboutus'>
                    <span className='fa fa-info fa-lg'></span> About Us
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className='nav-link' to='/menu'>
                    <span className='fa fa-list fa-lg'></span> Menu
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className='nav-link' to='/favorites'>
                    <span className='fa fa-heart fa-lg'></span> My Order
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className='nav-link' to='/contactus'>
                    <span className='fa fa-address-card fa-lg'></span> Contact
                    Us
                  </NavLink>
                </NavItem>
              </Nav>
              <Nav className='ml-auto' navbar>
                <NavItem>
                  {!this.props.auth.isAuthenticated ? (
                    <div>
                      <Button outline onClick={this.toggleModal}>
                        <span className='fa fa-sign-in fa-lg'></span> Login
                        {this.props.auth.isFetching ? (
                          <span className='fa fa-spinner fa-pulse fa-fw'></span>
                        ) : null}
                      </Button>
                      <Button outline onClick={this.toggleSignIn}>
                        <span className='fa'></span> Sign In
                      </Button>
                    </div>
                  ) : (
                      <div>
                        <div className='navbar-text mr-3'>
                          <h5 className='usernameColor'>
                            {this.props.auth.user.username}
                          </h5>
                        </div>
                        <Button outline onClick={this.handleLogout}>
                          <span className='fa fa-sign-out fa-lg'></span> Logout
                        {this.props.auth.isFetching ? (
                            <span className='fa fa-spinner fa-pulse fa-fw'></span>
                          ) : null}
                        </Button>
                      </div>
                    )}
                </NavItem>
                <NavItem>
                  <div></div>
                </NavItem>
              </Nav>
            </Collapse>
          </div>
        </Navbar>
        <Jumbotron>
          <div className='container'>
            <div className='row row-header'>
              <div className='col-12 col-sm-6'>
                {/* <h1>
                  <strong>Fancy Japanese Food</strong>
                </h1>
                <p>
                  <strong>
                    We take inspiration from the World's best cuisines, and
                    create a unique fusion experience. Our lipsmacking creations
                    will tickle your culinary senses!
                  </strong>
                </p> */}
              </div>
            </div>
          </div>
        </Jumbotron>
        <Modal isOpen={this.state.isSignInOpen} toggle={this.toggleSignIn}>
          <ModalHeader toggle={this.toggleSignIn}>Sign In</ModalHeader>
          <ModalBody>
            <Form onSubmit={this.handleSignIn}>
              <FormGroup>
                <Label htmlFor='username'>Username</Label>
                <Input
                  type='text'
                  id='username_signin'
                  name='username'
                  innerRef={input => (this.username = input)}
                />
              </FormGroup>
              <FormGroup>
                <Label htmlFor='firstname'>Firstname</Label>
                <Input
                  type='text'
                  id='firstname_signin'
                  name='firstname'
                  innerRef={input => (this.firstname = input)}
                />
              </FormGroup>
              <FormGroup>
                <Label htmlFor='lastname'>Lastname</Label>
                <Input
                  type='text'
                  id='lastname_signin'
                  name='lastname'
                  innerRef={input => (this.lastname = input)}
                />
              </FormGroup>
              <FormGroup>
                <Label htmlFor='password'>Password</Label>
                <Input
                  type='password'
                  id='password_signin'
                  name='password'
                  innerRef={input => (this.password = input)}
                />
              </FormGroup>
              <FormGroup check>
                <Label check>
                  <Input
                    type='checkbox'
                    name='remember'
                    innerRef={input => (this.remember = input)}
                  />
                  Remember me
                </Label>
              </FormGroup>
              <Button type='submit' value='submit' color='primary'>
                Sign In
              </Button>
            </Form>
          </ModalBody>
        </Modal>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
          <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
          <ModalBody>
            <Form onSubmit={this.handleLogin}>
              <FormGroup>
                <Label htmlFor='username'>Username</Label>
                <Input
                  type='text'
                  id='username'
                  name='username'
                  innerRef={input => (this.username = input)}
                />
              </FormGroup>
              <FormGroup>
                <Label htmlFor='password'>Password</Label>
                <Input
                  type='password'
                  id='password'
                  name='password'
                  innerRef={input => (this.password = input)}
                />
              </FormGroup>
              <FormGroup check>
                <Label check>
                  <Input
                    type='checkbox'
                    name='remember'
                    innerRef={input => (this.remember = input)}
                  />
                  Remember me
                </Label>
              </FormGroup>
              <Button type='submit' value='submit' color='primary'>
                Login
              </Button>
            </Form>
          </ModalBody>
        </Modal>
      </React.Fragment>
    );
  }
}

export default Header;



