import React from "react";
import {
  Breadcrumb,
  BreadcrumbItem,
  Card,
  CardBody,
  CardHeader,
  Media
} from "reactstrap";
import { Link } from "react-router-dom";
import { Loading } from "./LoadingComponent";
import { baseUrl } from "../shared/baseUrl";
import { Fade, Stagger } from "react-animation-components";

function RenderLeader({ leader }) {
  return (
    <div className='leaderform'>
      <div>
        <img
          className='cardimg-leader'
          object
          src={baseUrl + leader.image}
          alt={leader.name}
        />
      </div>
      <div>
        <p>
          <strong>{leader.name}</strong>
        </p>

        <p>
          <strong>{leader.designation}</strong>
        </p>
        <p>{leader.description}</p>
      </div>
    </div>
  );
}

function LeaderList(props) {
  const leaders = props.leaders.leaders.map(leader => {
    return (
      <Fade in key={leader._id}>
        <div className='col-12 mt-2'>
          <RenderLeader leader={leader} />
        </div>
      </Fade>
    );
  });

  if (props.leaders.isLoading) {
    return <Loading />;
  } else if (props.leaders.errMess) {
    return (
      <div className='col-12'>
        <h4>{props.leaders.errMess}</h4>
      </div>
    );
  } else {
    return (
      <Media list>
        <Stagger in>{leaders}</Stagger>
      </Media>
    );
  }
}

function About(props) {
  return (
    <div className='container'>
      <div className='row'>
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to='/home'>Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>About Us</BreadcrumbItem>
        </Breadcrumb>
        <div className='col-12'>
          <h3>About Us</h3>
          <hr />
        </div>
      </div>
      <div className='row row-content'>
        <div className='col-12 col-md-6'>
          <h2>Our History</h2>
          <p>
            Japanese cuisine is based on combining the staple food, which is
            steamed white rice or gohan, with one or several okazu or main
            dishes and side dishes. Started in 2013, Fancy Japanese food
            restaurant quickly established itself as a culinary icon par
            excellence in the US.
          </p>
        </div>
        <div className='col-12 col-md-5'>
          <Card>
            <CardHeader className='bg-primary text-white'>
              Facts At a Glance
            </CardHeader>
            <CardBody>
              <dl className='row p-1'>
                <dt className='col-6'>Started</dt>
                <dd className='col-6'>9 Sep. 2013</dd>
                <dt className='col-6'>Major Stake Holder</dt>
                <dd className='col-6'>Foods Inc.</dd>
                <dt className='col-6'>Last Year's Turnover</dt>
                <dd className='col-6'>$1,000</dd>
                <dt className='col-6'>Employees</dt>
                <dd className='col-6'>3</dd>
              </dl>
            </CardBody>
          </Card>
        </div>
        <div className='col-12'>
          <Card>
            <CardBody className='bg-faded'>
              <blockquote className='blockquote'>
                <p className='mb-0'>
                  You better cut the pizza in four pieces because I'm not hungry
                  enough to eat six.
                </p>
                <footer className='blockquote-footer'>
                  Yogi Berra,
                  <cite title='Source Title'>
                    The Wit and Wisdom of Yogi Berra, P. Pepe, Diversion Books,
                    2014
                  </cite>
                </footer>
              </blockquote>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className='row row-content'>
        <div className='col-12'>
          <h2 className='corporate'>Corporate Leadership</h2>
        </div>
        <LeaderList leaders={props.leaders} />
      </div>
    </div>
  );
}

export default About;



