import React, { Component } from "react";
import {
  Media, Breadcrumb, BreadcrumbItem, Button, CardImg, Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Link } from "react-router-dom";
import { baseUrl } from "../shared/baseUrl";
import { Loading } from "./LoadingComponent";

// Import CreditPay
import CreditPay from './PayComponent/CreditPay';

const RenderMenuItem = ({ dish, deleteFavorite }) => {
  return (
    <div className='favoritelist'>
      <div className='favoritepic col-12 col-sm-3 '>
        <CardImg
          style={{ width: "12rem" }}
          object
          width='10px'
          src={baseUrl + dish.image}
          alt={dish.name}
        />
      </div>
      <div className='facoritedes col-12 col-sm-6 '>
        <Media heading>
          <div>
            <p>
              <strong>{dish.name}</strong>{" "}
              <span className='price'>$ {dish.price}</span>
            </p>
          </div>
        </Media>
        <p>{dish.description}</p>
        <Button outline color='danger' onClick={() => deleteFavorite(dish._id)}>
          <span className='fa fa-times'></span>
        </Button>
      </div>
    </div>
  );
};


class Favorites extends Component {
  constructor(props) {
    super(props);
    this.state = {
      payment: [],
      isModalOpen: false
    };

    // if (this.props.favorites.favorites) {
    //   this.setState({ payment: this.props.favorites.favorites.dishes })
    // }

    this.togglePay = this.togglePay.bind(this);
    this.handlePay = this.handlePay.bind(this);

  }

  togglePay() {
    this.setState({
      isModalOpen: !this.state.isModalOpen,
      payment: []
    });
  }

  handlePay(event) {
    this.togglePay();
    event.preventDefault();
  }


  render() {
    if (this.props.favorites.isLoading) {
      return (
        <div className='container'>
          <div className='row'>
            <Loading />
          </div>
        </div>
      );
    } else if (this.props.favorites.errMess) {
      return (
        <div className='container'>
          <div className='row favoriteNologin'>
            {/* <h4>{props.favorites.errMess}</h4> */}
            <h4>
              You need to Login and add your favorites
                <br />
              (try to login Username: 123, Password: password)
              </h4>
          </div>
        </div>
      );
    } else if (this.props.favorites.favorites) {
      this.state.payment = [];  // set empty array as default and put dish into payment

      const favorites = this.props.favorites.favorites.dishes.map(dish => {
        // count payment
        let totalPayment = 0;
        console.log('Dishes.length: ', this.props.favorites.favorites.dishes.length);

        if (this.state.payment.length <= this.props.favorites.favorites.dishes.length) {
          this.state.payment.push(dish.price);
        }
        console.log('Payment.length: ', this.state.payment.length);
        console.log("payment: ", this.state.payment);
        for (let x in this.state.payment) {
          totalPayment += this.state.payment[x];
        }
        console.log("Total payment: ", totalPayment);

        if (this.state.payment.length === this.props.favorites.favorites.dishes.length) {
          return (
            <div key={dish._id} className='col-12 mt-5'>
              <RenderMenuItem dish={dish} deleteFavorite={this.props.deleteFavorite} />
              <div className='col-sm-12 Totalpayment'>
                <h4>
                  <strong>Total price</strong>{" "}
                  <span className='dollar'>$ {totalPayment}</span>
                  <span>
                    <Button outline onClick={this.togglePay}>
                      <span class='fa fa-credit-card fa-lg' aria-hidden='true'>
                        {" "}
                        pay
                        </span>
                    </Button>
                  </span>
                </h4>
              </div>
              <div>
                <Modal isOpen={this.state.isModalOpen} toggle={this.togglePay}>
                  <ModalHeader toggle={this.togglePay}>Credit Cards Payment</ModalHeader>
                  <ModalBody>
                    <CreditPay />
                  </ModalBody>
                </Modal>
              </div>
            </div>
          );
        } else {
          return (
            <div key={dish._id} className='col-12 mt-5'>
              <RenderMenuItem dish={dish} deleteFavorite={this.props.deleteFavorite} />
            </div>
          );
        }
      });

      return (
        <div className='container'>
          <div className='row'>
            <Breadcrumb>
              <BreadcrumbItem>
                <Link to='/home'>Home</Link>
              </BreadcrumbItem>
              <BreadcrumbItem active>My Order</BreadcrumbItem>
            </Breadcrumb>
            <div className='col-12'>
              <h3>My Order</h3>
              <hr />
            </div>
          </div>
          <div className='row'>
            <Media list>{favorites}</Media>{" "}
            {/** Thsi " {favorite} " is an object from above 'const favorites' */}
          </div>
        </div>
      );
    } else {
      return (
        <div className='container'>
          <div className='row favoriteNologin'>
            <h4>
              You have no favorites, please add your favorite from Menu
                <br />
              <br />
              <strong>Example:</strong>
            </h4>
            <img
              src='assets/images/favoriteDemo.png'
              height='80%'
              width='80%'
              alt='favoriteDemo'
            />
          </div>
        </div>
      );
    }
  }
};


export default Favorites;



